/* The actual timeline (the vertical ruler) */
.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden; }
  .timeline a {
    color: #343432 !important;
    font-weight: bold !important;
    text-decoration: none !important; }
    .timeline a:hover {
      color: #000; }

/* The actual timeline (the vertical ruler) */
.timeline::after {
  content: '';
  position: absolute;
  width: 6px;
  background-color: #363635;
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -3px;
  z-index: -1; }

/* Container around content */
.timeline__container {
  padding: 10px 40px;
  position: relative;
  background-color: inherit;
  width: 50%; }

/* The circles on the timeline */
.timeline__container::after {
  content: '';
  position: absolute;
  width: 25px;
  height: 25px;
  right: -12px;
  background-color: #c5d2d8;
  border: 4px solid #363635;
  top: 15px;
  border-radius: 50%;
  z-index: 1; }

.timeline__container.tl--grey::after {
  background-color: #c5d2d8;
  border: 4px solid #363635; }

.timeline__container.tl--pink::after {
  background-color: #ffaddc;
  border: 4px solid #DF007D; }

.timeline__container.tl--green::after {
  background-color: #66f9e8;
  border: 4px solid #07BFA9; }

.timeline__container.tl--yellow::after {
  background-color: #ffe4bd;
  border: 4px solid #ffa823; }

.timeline__container.tl--orange::after {
  background-color: #fdc9b4;
  border: 4px solid #e04305; }

/* Place the container to the left */
.left {
  left: 0; }

/* Place the container to the right */
.right {
  left: 50%; }

/* Add arrows to the left container (pointing right) */
.left::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  right: 30px;
  border: medium solid #c5d2d8;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent #c5d2d8; }

/* Add arrows to the right container (pointing left) */
.right::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px;
  border: medium solid #c5d2d8;
  border-width: 10px 10px 10px 0;
  border-color: transparent #c5d2d8 transparent transparent; }

.left.tl--yellow::before {
  border: medium solid #ffe4bd;
  border-width: 10px 0 10px 10px;
  border-color: transparent transparent transparent #ffe4bd; }
  @media screen and (max-width: 768px) {
    .left.tl--yellow::before {
      border-width: 10px 10px 10px 0;
      border-color: transparent #ffe4bd transparent transparent; } }

.right.tl--yellow::before {
